<template>
  <div>
    <template>

      <!-- 数据列表 -->
      <v-card elevation="16"
              max-width="1700"
              height="150"
              class="mx-auto"
              style="margin-top: -25px">

        <!-- 数据表格头部 -->
        <v-hover>
          <template v-slot:default="{ hover }">
            <div
                :class="`elevation-${hover ? 24 : 6}`"
                class="mx-auto pa-6 transition-swing pt-4 blue lighten-5 "
            >
              <!-- 提示框 -->
              <v-snackbar
                  v-model="snackbar"
                  :timeout="1000"
                  :value="true"
                  bottom
                  :color="state"
                  outlined
                  absolute
                  top
              >
                {{ text }}
              </v-snackbar>

              <v-card-title>
                任务管理
                <v-spacer></v-spacer>
              </v-card-title>

              <v-card-text>
                <v-col
                    cols="12"
                    sm="10"
                >
                  <!-- 刷新页面 -->
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn icon
                             @click="searchJobs(1)">
                        <v-icon
                            dark
                            v-bind="attrs"
                            v-on="on"
                            color="green"
                        >
                          mdi-cached
                        </v-icon>
                      </v-btn>
                    </template>
                    <span>刷新</span>
                  </v-tooltip>

                  <v-divider
                      class="mx-4"
                      vertical
                  ></v-divider>

                  <!-- 新增用户 -->
                  <v-dialog
                      v-model="dialog"
                      persistent
                      max-width="500px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                          color="primary"
                          dark
                          v-bind="attrs"
                          v-on="on"
                      >
                        新建任务
                      </v-btn>
                    </template>
                    <v-card>
                      <!-- 提示框 -->
                      <v-snackbar
                          v-model="addSnackbar"
                          :timeout="1000"
                          :value="true"
                          bottom
                          :color="state"
                          outlined
                          absolute
                          top
                      >
                        {{ text }}
                      </v-snackbar>

                      <v-card-title>
                        <span class="text-h5">新建任务</span>
                      </v-card-title>

                      <v-card-text>
                        <v-container>

                          <v-form
                              ref="form"
                              v-model="valid"
                              lazy-validation
                          >
                            <v-text-field
                                v-model="jobName"
                                :counter="10"
                                maxlength="10"
                                :rules="jobNameRules"
                                label="任务名称*"
                                required
                            ></v-text-field>

                            <v-text-field
                                v-model="jobKey"
                                :counter="20"
                                maxlength="20"
                                :rules="jobKeyRules"
                                label="任务KEY*"
                                hint="对应@TsJob注解中的参数"
                                required
                            ></v-text-field>

                            <v-text-field
                                v-model="jobDesc"
                                :counter="20"
                                :rules="jobDescRules"
                                label="任务描述"
                            ></v-text-field>

                            <v-text-field
                                v-model="jobCron"
                                label="CRON*"
                                hint="0/59 * * * * ?"
                                :rules="jobCronRules"
                                required
                            ></v-text-field>

                          </v-form>

                        </v-container>
                        <small>*号为必填项</small>
                      </v-card-text>

                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                            color="blue darken-1"
                            text
                            @click="closeDialog"
                        >
                          关闭
                        </v-btn>
                        <v-btn
                            color="blue darken-1"
                            text
                            :disabled="!valid"
                            class="mr-4"
                            @click="validate"
                        >
                          保存
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>

                  <v-divider
                      class="mx-4"
                      vertical
                  ></v-divider>

                </v-col>
              </v-card-text>
            </div>
          </template>
        </v-hover>
      </v-card>

      <v-card elevation="16"
              max-width="1700"
              class="mx-auto"
              height="660"
              style="margin-top: 15px">
        <!-- 数据表格 -->
        <v-data-table
            elevation="55"
            height="600"
            :headers="headers"
            :items="desserts"
            :search="search"
            sort-by="calories"
            class="elevation-1"
            :single-select="singleSelect"
            :items-per-page="itemsPerPage"
            :page.sync="page"
            v-model="selected"
            item-key="id"
            show-select
            hide-default-footer
            :loading="loading"
            loading-text="Loading... Please wait"
        >
          <template v-slot:top>
            <v-toolbar
                flat
            >

              <v-dialog v-model="dialogDelete" max-width="300px">
                <v-card>
                  <v-card-title class="text-h5">确定要删除此数据?</v-card-title>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="closeDelete">取消</v-btn>
                    <v-btn color="blue darken-1" text @click="deleteItemConfirm">确定</v-btn>
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>

          <template v-slot:item.actions="{ item }">
            <v-icon
                small
                class="mr-2"
                @click="runJob(item)"
            >
              mdi-play
            </v-icon>
            <v-icon
                small
                class="mr-2"
                @click="startJob(item)"
            >
              mdi-timer
            </v-icon>
            <v-icon
                small
                class="mr-2"
                @click="stopJob(item)"
            >
              mdi-lock
            </v-icon>
            <v-icon
                small
                class="mr-2"
                @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
                small
                @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </template>

        </v-data-table>

        <v-divider></v-divider>

        <!-- 分页组件 -->
        <div class="text-center pt-2" style="margin-top: -65px">
          <v-pagination
              v-model="page"
              :length="pageCount"
              circle
              @input="searchJobs"
          ></v-pagination>
        </div>


        <!-- 修改调度 -->
        <v-dialog
            v-model="editDialog"
            persistent
            max-width="500px"
        >
          <v-card>

            <v-snackbar
                v-model="editSnackbar"
                :timeout="1000"
                :value="true"
                bottom
                :color="state"
                outlined
                absolute
                top
            >
              {{ text }}
            </v-snackbar>

            <v-card-title>
              <span class="text-h5">修改调度</span>
            </v-card-title>

            <v-card-text>
              <v-container>

                <v-form
                    ref="editForm"
                    v-model="valid"
                    lazy-validation
                >
                  <v-text-field
                      v-model="editJob.jobName"
                      :counter="10"
                      maxlength="10"
                      :rules="jobNameRules"
                      label="任务名称*"
                      required
                  ></v-text-field>

                  <v-text-field
                      v-model="editJob.jobKey"
                      :rules="jobKeyRules"
                      label="任务KEY*"
                      hint="对应@TsJob注解中的参数"
                      maxlength="20"
                      required
                  ></v-text-field>

                  <v-text-field
                      v-model="editJob.jobDesc"
                      :counter="20"
                      :rules="jobDescRules"
                      label="任务描述"
                  ></v-text-field>

                  <v-text-field
                      v-model="editJob.jobCron"
                      label="CRON*"
                      hint="0/59 * * * * ?"
                      :rules="jobCronRules"
                      required
                  ></v-text-field>

                </v-form>

              </v-container>
              <small>*号为必填项</small>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                  color="blue darken-1"
                  text
                  @click="closeEditDialog"
              >
                关闭
              </v-btn>
              <v-btn
                  color="blue darken-1"
                  text
                  :disabled="!valid"
                  class="mr-4"
                  @click="editValidate"
              >
                保存
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

      </v-card>


    </template>
  </div>
</template>

<script>
export default {
  name: "Job",

  data() {
    return {
      dialogDelete: false,
      loading: false,
      showPwd: false,
      showCheckPwd: false,
      phone: "",
      jobCron: "",
      jobName: "",
      jobKey: "",
      jobDesc: "",
      valid: true,
      jobNameRules: [
        v => !!v || '任务名称不能为空',
        v => (v && v.length <= 10) || '任务名称必须小于10个字符',
      ],
      jobKeyRules: [
        v => !!v || '任务KEY不能为空',
        v => (v && v.length <= 20) || '任务KEY必须小于20个字符',
      ],
      jobDescRules: [
        v => !!v || '密码不能为空',
        v => (v && v.length >= 0 && v.length <= 255) || '密码长度必须为10~20字符之间',
      ],
      jobCronRules: [
        v => !!v || 'CRON不能为空'
      ],
      phoneRules: [
        v => !!v || '手机号不能为空',
        v => (v && v.length === 11) || '手机号必须为11位',
      ],
      dialog: false,
      editDialog: false,
      addSnackbar: false,
      editSnackbar: false,
      snackbar: false,
      state: "success",
      text: `查询失败`,
      page: 1,
      pageCount: 0,
      itemsPerPage: 10,
      selected: [],
      singleSelect: true,
      search: '',
      headers: [
        {
          text: 'ID',
          align: 'start',
          sortable: false,
          value: 'id',
          hidden: true
        },
        {text: '任务名称', value: 'jobName'},
        {text: '任务KEY', value: 'jobKey'},
        {text: '任务描述', value: 'jobDes'},
        {text: 'cron', value: 'cron'},
        {text: '任务状态', value: 'version'},
        {text: '操作', value: 'actions', sortable: false},
      ],
      desserts: [],
      editedIndex: -1,
      editJob: {
        id: "",
        jobName: "",
        jobDes: "",
        jobKey: "",
        cron: ""
      }
    }
  },

  watch: {
    dialog(val) {
      val || this.close()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    },
  },

  methods: {
    getContextPath() {
      const pathName = document.location.pathname;
      const index = pathName.substr(1).indexOf("/");
      const path = pathName.substr(0, index + 1);
      console.log(path)
      if (path === '/static') {
        return ""
      } else {
        return path
      }
    },
    closeDialog() {
      this.dialog = false
      this.$refs.form.reset()
    },
    closeEditDialog() {
      this.editDialog = false
      this.$refs.editForm.reset()
    },
    editValidate() {
      const flag = this.$refs.editForm.validate()
      if (flag) {
        this.$axios.post(this.getContextPath() + '/ts-job/edit-job', {
          'id': this.editJob.id,
          'jobName': this.editJob.jobName,
          'jobDes': this.editJob.jobDesc,
          'jobKey': this.editJob.jobKey,
          'cron': this.editJob.jobCron
        }).then((response) => {
          const code = response.data.code
          if (code === 200) {
            this.text = "修改成功"
            this.state = "success"
            this.editDialog = false
            this.state = "success"
            this.searchJobs(this.page)
          } else {
            this.text = "修改失败"
            this.text = response.data.msg
            this.state = "error"
          }
          this.editSnackbar = true
        }).catch((error) => {
          console.log(error)
          this.text = "请求失败"
          this.editSnackbar = true
          this.state = "error"
        })
      }
    },
    validate() {
      const flag = this.$refs.form.validate()
      if (flag) {
        this.$axios.post(this.getContextPath() + '/ts-job/add-job', {
          'jobName': this.jobName,
          'jobDes': this.jobDesc,
          'jobKey': this.jobKey,
          'cron': this.jobCron
        }).then((response) => {
          const code = response.data.code
          console.log(code)
          if (code === 200) {
            this.text = "新增成功"
            this.dialog = false
            this.$refs.form.reset()
            this.state = "success"
            this.searchJobs(this.page)
          } else {
            this.text = "新增失败"
            this.text = response.data.msg
            this.state = "error"
          }
          this.addSnackbar = true
        }).catch((error) => {
          console.log(error)
          this.text = "请求失败"
          this.addSnackbar = true
          this.state = "error"
        })
      }
    },
    searchJobs(page) {
      this.loading = true
      this.page = page;
      this.$axios.post(this.getContextPath() + '/ts-job/search-job', {
        'pageIndex': page,
        'pageSize': this.itemsPerPage
      }).then((response) => {
        this.snackbar = true
        this.loading = false
        this.text = "查询成功"
        this.state = "success"
        this.desserts = response.data.data.result
        this.pageCount = response.data.data.total
      }).catch((error) => {
        this.snackbar = true
        this.loading = false
        this.desserts = []
        this.text = "查询失败"
        this.state = "error"
        alert(error.response.data.msg)
      })
    },
    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editJob.id = item.id
      this.editJob.jobName = item.jobName
      this.editJob.jobKey = item.jobKey
      this.editJob.jobDesc = item.jobDes
      this.editJob.jobCron = item.cron
      this.editDialog = true
    },
    runJob(item) {
      this.$axios.get(this.getContextPath() + '/ts-job/run-job?jobKey=' + item.jobKey).then((response) => {
        const code = response.data.code
        if (code === 200) {
          this.snackbar = true
          this.loading = false
          this.text = "执行成功"
          this.state = "success"
        } else {
          this.snackbar = true
          this.loading = false
          this.text = response.data.msg
          this.state = "error"
        }
      }).catch(() => {
        this.snackbar = true
        this.loading = false
        this.desserts = []
        this.text = "执行失败"
        this.state = "error"
      })
    },
    startJob(item) {
      this.$axios.get(this.getContextPath() + '/ts-job/start-scheduler?jobKey=' + item.jobKey).then((response) => {
            const code = response.data.code
            if (code === 200) {
              this.snackbar = true
              this.loading = false
              this.text = "执行成功"
              this.state = "success"
              this.searchJobs(this.page)
            } else {
              this.snackbar = true
              this.loading = false
              this.text = response.data.msg
              this.state = "error"
            }
          }
      ).catch(() => {
        this.snackbar = true
        this.loading = false
        this.desserts = []
        this.text = "执行失败"
        this.state = "error"
      })
    },
    stopJob(item) {
      this.$axios.get(this.getContextPath() + '/ts-job/stop-scheduler?jobKey=' + item.jobKey).then((response) => {
        const code = response.data.code
        if (code === 200) {
          this.snackbar = true
          this.loading = false
          this.text = "执行成功"
          this.state = "success"
          this.searchJobs(this.page)
        } else {
          this.snackbar = true
          this.loading = false
          this.text = response.data.msg
          this.state = "error"
        }
      }).catch(() => {
        this.snackbar = true
        this.loading = false
        this.desserts = []
        this.text = "执行失败"
        this.state = "error"
      })
    }
    ,
    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.dialogDelete = true
    }
    ,
    deleteItemConfirm() {
      let id = this.desserts[this.editedIndex].id
      this.$axios.get(this.getContextPath() + '/ts-job/del-job?id=' + id).then(() => {
        this.searchJobs(this.page)
      })
      this.text = "删除成功"
      this.snackbar = true
      this.closeDelete()
    }
    ,
    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedIndex = -1
      })
    }
    ,
    closeDelete() {
      this.dialogDelete = false
    }
    ,
  },
  mounted() {
    this.loading = true
    this.$axios.post(this.getContextPath() + '/ts-job/search-job', {
      'pageIndex': 1,
      'pageSize': 10
    }).then((response) => {
      this.snackbar = true
      this.loading = false
      this.text = "查询成功"
      this.state = "success"
      this.desserts = response.data.data.result
      this.pageCount = response.data.data.total
    }).catch((error) => {
      this.snackbar = true
      this.loading = false
      this.desserts = []
      this.text = "查询失败"
      this.state = "error"
      alert(error.response.data.msg)
    })
  }
}
</script>

