<template>
  <div>
    <div>
      <v-responsive
          max-width="700"
          class="mx-auto mb-4"
          style="margin-top: -40px"
      >
      </v-responsive>

      <v-card
          elevation="16"
          max-width="1700"
          class="mx-auto"
      >

        <v-hover>
          <template v-slot:default="{ hover }">
            <div
                :class="`elevation-${hover ? 24 : 6}`"
                class="mx-auto pa-6 transition-swing pt-4 orange lighten-4 white--text"
            >
              <v-card-title>
                tech service job log
                <v-spacer></v-spacer>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn icon @click="reloadData">
                      <v-icon
                          color="green"
                          v-bind="attrs"
                          v-on="on"
                      >
                        mdi-cached
                      </v-icon>
                    </v-btn>
                  </template>
                  <span>刷新</span>
                </v-tooltip>

                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn icon @click="clearData">
                      <v-icon
                          color="red"
                          v-bind="attrs"
                          v-on="on"
                      >
                        mdi-delete
                      </v-icon>
                    </v-btn>
                  </template>
                  <span>清空</span>
                </v-tooltip>

              </v-card-title>

              <v-card-text>
                TS-JOB执行日志
              </v-card-text>
            </div>
          </template>
        </v-hover>

        <v-divider></v-divider>
      </v-card>

      <v-card
          elevation="16"
          max-width="1700"
          height="620"
          class="mx-auto"
          style="margin-top: 15px"
      >
        <v-virtual-scroll
            :items="items"
            height="610"
            item-height="64"
            @scroll="scrollGet($event)"
            style="margin-top: 10px"
        >

          <template v-slot:default="{ item }">
            <v-list-item>
              <v-list-item-title hidden>{{ item.id }}</v-list-item-title>
              <v-list-item-avatar width="120">
                <v-avatar
                    color="blue lighten-4"
                    rounded
                    size="27"
                >
                  {{ item.logLevel }}
                </v-avatar>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>{{ item.method }}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-content>
                <v-list-item-title>{{ item.desc }}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-content>
                <v-list-item-title>{{ item.address }}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-content>
                <v-list-item-title>{{ item.time }}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-content>
                <v-list-item-title>{{ item.user }}</v-list-item-title>
              </v-list-item-content>
              <v-list-item-action>
                <v-btn
                    class="ma-2"
                    color="red"
                    dark
                    @click="deleteLog(item.id)"
                >
                  删除
                  <v-icon
                      dark
                      right
                  >
                    mdi-cancel
                  </v-icon>
                </v-btn>
              </v-list-item-action>
            </v-list-item>
            <v-divider></v-divider>
          </template>

        </v-virtual-scroll>

      </v-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
      page: 1
    }
  },
  mounted() {
    this.initData(1)
  },
  methods: {
    reloadData() {
      this.initData(1)
    },
    clearData() {
      this.$axios.delete('maintain/api/system/clearLog/sys_log'
      ).then(() => {
        this.initData(1)
      }).catch((error) => {
        this.desserts = []
        alert(error.response.data.msg)
      })
    },
    initData(page) {
      this.$axios.post('maintain/api/system/getLogs', {
        'pageIndex': page,
        'pageSize': 20
      }).then((response) => {
        this.items = response.data.data.result
      }).catch((error) => {
        this.desserts = []
        alert(error.response.data.msg)
      })
    },
    deleteLog(id) {
      this.$axios.delete('maintain/api/system/delLog/' + id
      ).then(() => {
        this.initData(1)
      }).catch((error) => {
        this.desserts = []
        alert(error.response.data.msg)
      })
    },
    scrollGet(e) {
      if (e.target.scrollTop + e.target.offsetHeight > e.target.scrollHeight - 100) {
        this.page = this.page + 1
        this.$axios.post('maintain/api/system/getLogs', {
          'pageIndex': this.page,
          'pageSize': 20
        }).then((response) => {
          this.items = [...this.items, ...response.data.data.result]
        }).catch((error) => {
          this.desserts = []
          alert(error.response.data.msg)
        })
      }
    }
  }
}
</script>

